.table__col tr td {
  font-size: 14px;
  padding: 10px 5px;
  padding-bottom: 11px;
  font-weight: 600;
}
.tabs__list {
  display: flex;
  gap: 0 16px;
  align-items: center;
  border-bottom: 1px solid #DFDFE0;
  margin-bottom: 20px;
}
.tabs__box {
  position: relative;
  cursor: pointer;
  font-size: 12px;
  color: #4F4F4F;
  padding-bottom: 10px;
  font-weight: 600;
}
.tabs__box:after {
  position: absolute;
  content: "";
  bottom: -1.5px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: transparent;
}
.tabs__box.active {
  color: #539100;
}
.tabs__box.active:after {
  background-color: #539100;
}
.tabs__item {
  counter-reset: list-tabs;
}
.tabs__col {
  display: flex;
  counter-increment: list-tabs;
  position: relative;
  border-radius: 6px;
  padding-left: 40px;
}
.tabs__col:not(:last-child) {
  margin-bottom: 6px;
}
.tabs__col:after {
  position: absolute;
  content: counter(list-tabs);
  top: 0;
  left: 0;
  width: 30px;
  height: 100%;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.7);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(250, 199, 30, 0.15);
  border-radius: 6px 0 0 6px;
  font-weight: 600;
}
.tabs__col:nth-child(4n-3) {
  border: 1px solid #FAC71E;
}
.tabs__col:nth-child(3n-1) {
  border: 1px solid #C0C8D0;
}
.tabs__col:nth-child(3n-1):after {
  background-color: rgba(192, 200, 208, 0.15);
}
.tabs__col:nth-child(3n) {
  border: 1px solid #FCA16E;
}
.tabs__col:nth-child(3n):after {
  background: rgba(252, 161, 110, 0.15);
}
.tabs__value {
  flex: 1;
  display: flex;
  align-items: center;
  height: 36px;
  font-size: 14px;
  line-height: 1.35;
  font-weight: 600;
}
.tabs__value--btn {
  cursor: pointer;
  flex: initial;
  width: 30px;
  justify-content: center;
  border: none;
  padding: 0;
  background-color: transparent;
}
.tabs__value--percent {
  color: #539100;
}
.trading__wrap {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 0;
  margin: 0 -5px;
  margin-bottom: 30px;
}
@media screen and (max-width: 840px) {
  .trading__wrap {
    margin: 0;
    margin-bottom: 20px;
  }
}
.trading__item {
  width: calc(33.3333333333% - 10px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(204, 82, 73, 0.25);
  border-radius: 10px;
  margin: 0 5px;
}
@media screen and (max-width: 840px) {
  .trading__item {
    width: 100%;
    margin: 0;
  }
}
@media screen and (max-width: 767px) {
  .trading__item {
    max-width: 450px;
    margin: 0 auto;
  }
}
.trading__item:nth-child(2) .trading__title {
  color: #CC5249;
  background: rgba(204, 82, 73, 0.07);
}
.trading__item:nth-child(3) .trading__title {
  color: #292E33;
  background: rgba(41, 46, 51, 0.07);
}
.trading__info {
  padding: 16px;
  padding-bottom: 10px;
}
.trading__title {
  border-radius: 9px;
  background: rgba(113, 178, 25, 0.07);
  text-align: center;
  font-size: 18px;
  line-height: 1.3;
  color: rgb(83, 145, 0);
  text-align: center;
  padding: 7px 16px;
  width: max-content;
  margin: 0 auto;
  margin-bottom: 8px;
  font-weight: 600;
}
@media screen and (max-width: 840px) {
  .trading__title {
    font-size: 16px;
  }
}
.trading__text p {
  font-size: 16px;
  line-height: 1.5;
  color: rgb(0, 0, 0);
  text-align: center;
}
@media screen and (max-width: 840px) {
  .trading__text p {
    font-size: 14px;
  }
}
.trading__elem {
  width: 100%;
  font-size: 18px;
  line-height: 1.65;
  color: rgb(0, 0, 0);
  text-align: center;
  background-color: #F0F1F2;
  border-radius: 0 0 10px 10px;
  text-align: center;
  padding: 6px 16px;
  font-weight: 600;
}
.trading__block {
  background-color: #F4F7FE;
  border-left: 5px solid #2C64EB;
  padding: 16px;
  margin-bottom: 26px;
}
@media screen and (max-width: 767px) {
  .trading__block {
    margin-bottom: 20px;
  }
}
.trading__desc {
  margin-bottom: 10px;
}
.trading__desc p {
  font-size: 16px;
  line-height: 1.75;
  color: rgb(0, 0, 0);
  text-align: left;
}
@media screen and (max-width: 767px) {
  .trading__desc p {
    font-size: 14px;
  }
}
.trading__link {
  cursor: pointer;
  background-image: url(../../src/img/icons/arrow.svg);
  background-repeat: no-repeat;
  background-position: right 22px center;
  display: inline-block;
  font-size: 16px;
  line-height: 1.7;
  color: #1873EB;
  border-radius: 10px;
  background-color: rgba(44, 100, 235, 0.05);
  padding: 6px 16px;
  padding-right: 38px;
  font-weight: 600;
}
@media screen and (max-width: 767px) {
  .trading__link {
    font-size: 14px;
    padding: 7px 16px;
  }
}